'use client'

import { Card, CardContent } from "@/components/ui/card"
import { format } from "date-fns"
import { ptBR } from "date-fns/locale"
import { Loader2 } from "lucide-react"
import { useEffect, useState } from "react"




interface Dados {
    id: string,
    date: Date,
    distance: number,
    pace: number | null,
    calories: number,
    duration: number,
    createdAt: Date,
    updatedAt: Date
}



type ChartType = "line" | "bar" | "pie"


export default function Grafico() {
    const [workout, setWorkout] = useState<Dados[]>([])
    const [loading, setLoading] = useState(false)
    const [chartType, setChartType] = useState<ChartType>()
    const [time, setTime] = useState<"7 days" | "30 days" | "90 days" | "all">("30 days")


    // Fazer um fetch

    const fetchWorkout = async () => {
        try {
            setLoading(true)
            const response = await fetch('api/training_session')

            if(!response.ok) {
              throw new Error('Erro ao fazer a requisição')
            }

            const data = await response.json()

            setWorkout(data)
        } catch(e) {
            console.log(e)
            throw new Error('Erro ao fazer a requisição')
        } finally {
            setLoading(false)
        }
    }




   //Sempre faça o useEffect para não dar looping infinito
   useEffect(()=>{
    fetchWorkout()
   }, [])



   const filterWorkout = () => {
    const now = new Date()
    let daysAgo = new Date()

    switch(time) {
       case "7 days":
        daysAgo.setDate(now.getDate() - 7)
        break


       case "30 days":
        daysAgo.setDate(now.getDate() - 30)
        break


       case "90 days":
        daysAgo.setDate(now.getDate() - 90)
        break


       case "all":
       return workout
    }

    return workout.filter(w => new Date(w.date) >= daysAgo )
   }


    // Prepara os dados para o gráfico de LINHA (distancia x data)

    const preparaLine = () => {
        const filtered = filterWorkout()

        return filtered
        .sort((a, b) => new Date(a.date).getDate() - new Date(b.date).getDate())
        .map((workout) => ({
            date: format(new Date(workout.date), "dd/MM", {locale: ptBR}),
            fulldate: format(new Date(workout.date), "dd/MM/yyyy", {locale: ptBR}),
            distance: workout.distance,
            calories: workout.calories,
            duration: workout.duration,
            pace: workout.pace || workout.duration / workout.distance
        }))
    }





    // Prepara os dados para o gráfico de BARRAS( CALORIAS X DATA)

    const preparaBarra = () => {
        const filtered = filterWorkout()
        return filtered
        .sort((a, b) => new Date(a.date).getDate() - new Date(b.date).getDate())
        .map(workout => ({
            date: format(new Date(workout.date), "dd/MM", {locale: ptBR}),
            calories: workout.calories,
            distance: workout.distance
        }))
    }



    // Prepara dados para a pizza

    const preparaPizza = () => {
        const filtered = filterWorkout()

        const distanceRanges = [
            {range: "0-3 km", min: 0, max: 3, count: 0, color: "#0088FE" } ,
            { range: "3-5 km", min: 3, max: 5, count: 0, color: "#00C49F" },
            { range: "5-10 km", min: 5, max: 10, count: 0, color: "#FFBB28" },
            { range: "10+ km", min: 10, max: Infinity, count: 0, color: "#FF8042" },
        ]

        filtered.forEach(workout => {
            for(const range of distanceRanges){
                range.count++
                break
            }
        })
        return distanceRanges.filter(e => e.count > 0)

    }





    // Estatisticas  gerais

    const gerais = () => {
        const filtered = filterWorkout()

        if(filtered.length === 0){
            return {
                totalDistance: 0,
                totalCalories: 0,
                avgDistance: 0,
                avgPace: 0,
                totalWorkouts: 0, 
            }
        }


        const totalDistance = filtered.reduce((sum, w) => w.distance + sum, 0)
        const totalCalories = filtered.reduce((sum, w) => w.calories + sum, 0)
        const totalDuration = filtered.reduce((sum, w) => w.duration + sum, 0)
        const avgDistance = totalDistance / filtered.length;
        const avgPace = totalDuration / totalDistance;


        return {
            totalDistance: parseFloat(totalDistance.toFixed(2)),
            totalCalories: parseFloat(totalCalories.toFixed(2)),
            totalDuration: parseFloat(totalDuration.toFixed(2)),
            avgDistance: parseFloat(avgDistance.toFixed(2)),
            avgPace: parseFloat(avgPace.toFixed(2))
        }
    }



    if(loading) {
        return(
            <Card>
                <CardContent>
                    <Loader2 />
                    <span> Carregando dados</span>
                </CardContent>
            </Card>
        )
    }






















} 